ks-zaufane {
    display: flex;
    justify-content: center;
    padding: 0px;
    margin: 20px 0px 0px 0px;

    height: 150px;
}

ks-zaufane[home] #ekw70.ekw70 .review-text-section {
    background-color: #ffffff;
}

ks-zaufane[listing] {
    z-index: 10;
    padding: 20px;
    margin: 0px;
}

ks-zaufane[listing]:not([nobg]) {
    background: white;
}

ks-zaufane[listing][nobg] #ekw70.ekw70 .review-text-section {
    background-color: #ffffff;
}

@media screen and (max-width: 767px) {
    ks-zaufane {
        height: 350px;
        padding: 0px !important;
        margin: 0px;

        background: linear-gradient(180deg, 
            #ffc400 0%, 
            #ffc400 42.85714285714286%, 
            #ffffff 42.85714285714286%, 
            #ffffff 100%
        ) !important;
    }

    ks-zaufane[listing]:not([nobg]) {
        background: linear-gradient(180deg, 
            #ffc400 0%, 
            #ffc400 42.85714285714286%, 
            #e5e4e2 42.85714285714286%, 
            #e5e4e2 100%
        ) !important;
    }
}