import { Component, h, Prop, Build } from '@stencil/core';

declare var window: any;

@Component({
    tag: 'ks-zaufane',
    styleUrl: 'zaufane.css',
})
export class Zaufane {
    @Prop() token: string;
    @Prop() customer: string;

    @Prop({reflect: true}) home: boolean = false;
    @Prop({reflect: true}) listing: boolean = false;
    @Prop({reflect: true}) nobg: boolean = false;

    componentDidLoad() {
        if(Build.isBrowser) {
            var k = document.getElementsByClassName("ekomi-widget-" + this.token);
            for(var x=0;x<k.length;x++){ this.registerWidget(window, this.token); }
        }
    }

    registerWidget (w, token) {
        w['_ekomiWidgetsServerUrl'] = 'https://widgets.ekomi.com';
        w['_customerId'] = this.customer;
        if (w['_language'] == undefined) {
            w['_language'] = new Array();
        }
        w['_language'][token] = 'auto';        
        
        if(typeof(w['_ekomiWidgetTokens']) !== 'undefined'){
            w['_ekomiWidgetTokens'][w['_ekomiWidgetTokens'].length] = token;
        } else {
            w['_ekomiWidgetTokens'] = new Array(token);
        }
        if(typeof(window?.ekomiWidgetJs) == 'undefined') {
            window.ekomiWidgetJs = true;
            var scr = document.createElement('script');scr.src = 'https://sw-assets.ekomiapps.de/static_resources/widget.js';
            var head = document.getElementsByTagName('head')[0];head.appendChild(scr);
            
        } else {if (typeof window.ekomiWidgetMain != 'undefined') {window.ekomiWidgetMain('ajax', token);}}
        return true;
    }

	render() {
		return [
            <div id="widget-container" class={"ekomi-widget-container ekomi-widget-" + this.token}></div>,
            <a href="https://www.ekomi-pl.com/opinie-kuchniasklep.html" target="_blank">
                <img alt="kuchniasklep.pl Reviews with ekomi-pl.com" src="https://smart-widget-assets.ekomiapps.de/resources/ekomi_logo.png" style={{display: "none"}}/>
            </a>
        ];
	}
}
